@import url(https://fonts.googleapis.com/css2?family=Spartan:wght@300&display=swap);
body {
    margin: 0;
}
.App {
  height: 100vh;
  box-sizing: border-box;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: 'Spartan', sans-serif;
}

h1 {
padding: 0 0 20px 0;
}

.gradientBackground {
  z-index: -1;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

img {
  height: 400px;
  width: 400px;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}


.gradient {
  background: linear-gradient(-45deg, #c1a7a7, #98779f, #9298da, #6d5998);
  background-size: 200% 200%;
  -webkit-animation: Gradient 20s ease infinite;
  animation: Gradient 20s ease infinite;
}

.gradient.liberal {
  background: linear-gradient(-45deg, #ae3c62, #d1b5ba, #a58596, #9a2c2c);
  background-size: 400% 200%;
  -webkit-animation: Gradient 20s ease infinite;
  animation: Gradient 20s ease infinite;
}

.gradient.conservative {
  background: linear-gradient(-45deg, #4d5d91, #7caec7, #8cc7da, #2560ac);
  background-size: 400% 200%;
  -webkit-animation: Gradient 20s ease infinite;
  animation: Gradient 20s ease infinite;
}

@-webkit-keyframes Gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

@keyframes Gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

